.page {
    position: relative;
    width: calc(100% - 40px);
    /* min-height: calc(100vh - 40px); */
    overflow: hidden;
    padding: 20px;
    background: var(--bg);
    z-index: 0;
}
.page.fixed {
    height: calc(100vh - 40px);
}
.page.min {
    min-height: calc(100vh - 40px);
}
.page.normal .bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    background: rgba(46, 46, 46, 0.6);
    z-index: 0;
}
.page img.background {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.page img.background.blurred {
    opacity: 0.1;
}